<template>
  <div class="page_wrap">
    <aside class="reset_wrap" @click="resetInfo">set</aside>

    <section class="header_wrap">
      <p class="online_num">当前在线人数{{ onlineNum }}</p>
    </section>
    <section class="password_wrap" v-if="!isLogin">
      <aside class="pwd_tips_wrap">
        <div class="pwd_input_wrap">
          <input class="pwd_input_area" placeholder="请输入绝密id" type="text " @keydown.enter="doConfirm" v-model="uniqId" />
        </div>

        <div class="pwd_input_wrap">
          <input class="pwd_input_area" placeholder="请输入名字" type="text " @keydown.enter="doConfirm" v-model="userInfo.name" />
        </div>

        <aside class="btn_confirm" @click="doConfirm">确认</aside>
      </aside>
    </section>

    <!-- 消息窗口 -->
    <section class="chat_wrap" ref="chatDomWrap">
      <div class="msg_wrap_container" ref="chatDom">
        <div class="msg_wrap" v-for="(item, index) in filterChatList" :key="index" :class="{isMe: item.__isMe}">
          <aside class="user_wrap" v-if="!item.__isMe">{{ item.name }}</aside>
          <aside class="msg" :class="{isMe: item.__isMe}">{{ item.msg }}</aside>
          <aside class="user_wrap" v-if="item.__isMe">{{ item.name }}</aside>
        </div>
      </div>
    </section>

    <section class="input_wrap">
      <input class="input_area" @keydown.enter="doClick" type="text " v-model="curContent" />
      <aside class="btn_send" @click="doClick">send</aside>
    </section>
  </div>
</template>

<script>
import io from "socket.io-client";
const socket = io("ws://106.12.161.139:3000");
// const socket = io("ws://localhost:3000");

let uniqId = "1587376316756";
export default {
  data: function() {
    return {
      uniqId: "",
      isLogin: false,
      password: "",
      onlineNum: 0,
      pwdList: [
        {
          pwd: 1587376316756,
          title: "小恩爱"
        }
      ],
      curContent: "",
      userInfo: {
        id: "",
        name: ""
      },
      userList: [
        {
          uid: "007525639943605444",
          name: "德龙"
        },
        {
          uid: "7980809627105447",
          name: "feifei"
        }
      ],
      chatList: [
        // {
        //   uid: "jjljlkjljl",
        //   name: "龙",
        //   msg: "开跌？"
        // }
      ]
    };
  },
  watch: {
    chatList() {
      this.$nextTick(() => {
        this.$refs.chatDomWrap.scrollTo(0, this.$refs.chatDom.offsetHeight);
      });
    }
  },
  computed: {
    filterChatList() {
      let filterChatList = [];
      let chatList = this.chatList;
      for (let i = 0; i < chatList.length; i++) {
        let __isMe = false;
        if (chatList[i].uid === this.userInfo.uid) {
          __isMe = true;
        }

        filterChatList.push({...chatList[i], __isMe});
      }
      return filterChatList;
    }
  },
  created() {
    document.title = "小恩爱";
    if (!this.userInfo.uid) {
      let userInfo = window.localStorage.getItem("userInfo");
      console.log("[log]===>localStorage.getItem", userInfo);

      if (userInfo) {
        let [uid, name, uniqId] = userInfo.split("-");
        this.userInfo = {uid, name, uniqId};
        this.isLogin = true;
      } else {
        console.log("[log]===> 没有用户信息，请输入");
        this.isLogin = false;
      }
    } else {
      console.log("[log]===> 已经有用户信息了");
    }

  },
  mounted() {
    socket.on("connect", () => {
      if (this.userInfo.uniqId) {
        socket.on(`sendMsg_${this.userInfo.uniqId}`, msg => {
          this.chatList.push(msg);
        });
      }

      socket.on("showOnlineNum", msg => {
        this.onlineNum = msg.num;
      });
    });
  },
  methods: {
    resetInfo() {
      this.isLogin = false;
    },
    doConfirm() {
      let checkThrough = this.pwdList.filter(item => {
        console.log(`pwd==>${item.pwd}, id===>${this.uniqId}`);
        return +item.pwd === +this.uniqId;
      });
      if (checkThrough.length) {
        console.log("[log]==> 检验通过");
        window.localStorage.setItem("userInfo", `${Date.now()}-${this.userInfo.name}-${this.uniqId}`);
        this.isLogin = true;
      } else {
        console.log("[log]==>检验失败");
        // alert('密码匹配失败')
      }
    },
    doClick() {
      if (!this.curContent) {
        console.log("sorry, you aren't able to send empty msg");
        return;
      }

      let msgObj = {
        msg: this.curContent,
        ...this.userInfo
      };

      this.curContent = "";

      console.log("[log]==> $emit ", `sendMsg_${this.userInfo.uniqId}`);

      socket.emit(`sendMsg_${this.userInfo.uniqId}`, msgObj);
    }
  }
};
</script>
